import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { PartyListComponent } from './party-list/party-list.component';
import { PartyGroupListComponent } from './party-group-list/party-group-list.component';
import { PartyComponent } from './party/party.component';
import { CustomerListComponent } from './customer-list/customer-list.component';
// import { PartyComponent } from './party-party.component';

const routes: Routes = [
  {
    path: '',
    component: PartyComponent,
    children: [
      {
        path: '',
        redirectTo: 'party-list',
        pathMatch: 'full'
      },
      {
        path: 'customer-list',
        component: CustomerListComponent

      },

      {
        path: 'party-list',
        component: PartyListComponent
      },
      {
        path: 'party-group-list',
        component: PartyGroupListComponent
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PartyRoutingModule { }
