import { TitleCasePipe } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { PartGroupServiceService } from 'src/app/core/services/part-group-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { AddGroupMultiplePartyComponent } from '../add-group-multiple-party/add-group-multiple-party.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { PartyGroupAddComponent } from '../party-group-add/party-group-add.component';
import { PartyService } from 'src/app/core/services/party.service';
import { ViewPartyGroupComponent } from '../view-party-group/view-party-group.component';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { F } from '@angular/cdk/keycodes';
import { Subject, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap } from 'rxjs/operators';

@Component({
  selector: 'app-party-group-list',
  templateUrl: './party-group-list.component.html',
  styleUrls: ['./party-group-list.component.scss']
})
export class PartyGroupListComponent implements OnInit {
  // public partyGroupData:any=[];
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public p = 1;
  public currentpage: any;
  public totalrow: number = 10;
  public toatlPages: any;
  public currentPageNo: number = 0;
  public partyGrpPageSize: number = 5;
  public pagesize: number = 1;
  public count: number = 0;
  public groupId:any;
  searchGroupName: string = '';
  private searchSubject = new Subject<string>(); 
  private searchSubscription: Subscription;
  isLoading: boolean = false;

  public checkSingleArray: any = [];
  public isChecked: boolean = false;
  public upiCheckedId = [];
  public allSelect: boolean = false;
  public checkSelect: boolean = false;
  public checkSelectsingle:boolean =false;
  public form: FormGroup;

  @Input() partyGroupData: any[] = []  ;
  excelFileUpload: any;
  pageNumberDisplay: any[] = [];
  selectedPartyGrpIds: any[] = [];

  constructor(private partyGroupservice:PartGroupServiceService,
    public toastService: ToastNotificationService, public dialog: MatDialog,
    private titleCasePipe: TitleCasePipe, private route: ActivatedRoute, public partyservice : PartyService,private formBuilder: FormBuilder,) { }

  ngOnInit(): void {
    // this.route.queryParams.subscribe(params => {
    //   this.partyGroupData = params['partyGroupData'];
    // });
    this.getGroupDetails();
    this.partyservice.partyAdded$.subscribe(() => {
      // Fetch the updated list of group
      this.getGroupDetails();
    });

    this.searchSubscription = this.searchSubject
      .pipe(debounceTime(800),
        distinctUntilChanged(),
        tap(() => {
          this.isLoading = true; // Show the loader when the API call is initiated
        })).subscribe(searchKey => {
          // Call the API with the search key
          if (searchKey.length >= 3) {
            this.searchByGroupName(searchKey);
          } else if (searchKey === '') {
            // this.isLoading = true; // Hide the loader if the search key is less than the threshold
            this.searchByGroupName(searchKey);
          } else {
            this.isLoading = false; // Hide the loader if the search key is less than the threshold
          }
        });

    this.form = this.formBuilder.group({
      checkArray: this.formBuilder.array([]),
    });

  }

  getPartyNames(parties: any[]): string {
    const partyNames = parties.map(party => party.partyName);
    if (partyNames.length > 10) {
      const displayedNames = partyNames.slice(0, 10);
      return `${displayedNames.join(', ')} ...`;
    }
    return partyNames.join(', ');
  }  

  getGroupDetails(){
    this.partyGroupservice.getPartiGroupsDetails({}, this.currentUser.id, this.partyGrpPageSize, (this.currentPageNo + 1)).then((res) => {
      if(res){
        this.partyGroupData = res.data ? res.data.pageData.map((item) => item.group) : [];
        this.p = parseInt( res.data ? res.data.currentPage : 0,10);
        this.totalrow = res.data ? res.data.totalRows:0
        this.toatlPages = res.data ? res.data.totalPages:0

      }
  
    },(err) => {
      if(err.error.expose){
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else{
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

  GetGroupById(groupId){
    this.groupId = groupId
  }

  deleteGroup(){
    this.partyGroupservice.deleteGroup({},this.groupId).then((res:any)=>{
      this.toastService.toastMsg({
        title: "Success",
        content: "Group Deleted Successfully!!!"
      });
      // this.toastService.openSnackBar("Group Deleted Successfully!!!");
      this.getGroupDetails();
    },(err) => {
      if(err.error.expose){
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else{
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }

  openEditPartyGrpModal(groupId: string): void {
    const dialogRef = this.dialog.open(AddGroupMultiplePartyComponent, {
      width: '900px', // Adjust the width as per your requirement
      height: '100vh', // Adjust the height as per your requirement
      data: { groupId: groupId },
      panelClass: 'custom-dialog-edit-PartyGrp',
      disableClose:true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe(result => {
      
    });
  }

  onSearchInputChange(searchKey: string) {
    // Emit the search key to the searchSubject
    this.searchSubject.next(searchKey);
  }


  searchByGroupName(searchKey: string) {
    if (searchKey.trim() === '') {
      this.getGroupDetails();
    } else {
      this.partyGroupservice.getPartiGroupsDetailsByName({}, this.currentUser.id, { groupName: searchKey })
        .then((res) => {
          this.partyGroupData = res.data.pageData.map((item) => item.group);
          this.partyGroupData = this.partyGroupData.filter(group => {
            return group.groupName.toLowerCase().includes(searchKey.toLowerCase()) || group.groupDescription.toLowerCase().includes(searchKey.toLowerCase())});
          if(this.partyGroupData.length === 0){
            this.toastService.toastMsg({
              title: "Error",
              content: searchKey + " Is Not Found"
            });
            // this.toastService.openErrorSnackBar(searchKey + " is not found");
          }
        })
        .catch((error) => {
          this.toastService.toastMsg({
            title: "Error",
            content: "error",
          });
          // this.toastService.openErrorSnackBar(error);
          console.log('Error:', error);
        });
    }
    this.isLoading = false; 
  }

  openDialogAddGrp(): void {
    const dialogRef: MatDialogRef<PartyGroupAddComponent> = this.dialog.open(PartyGroupAddComponent, {
      width: '650px',
     // height : '100vh',
      data: { },
      panelClass: 'custom-dialog-PartyGrp-class',
      disableClose:true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  
  handleInputChange(e){
    this.excelFileUpload= e.target.files[0];
    if(this.excelFileUpload){
      let formdata = new FormData();
      formdata.set("file", this.excelFileUpload)
      this.partyservice.postBulkParty(formdata, this.currentUser.id).then((res) => {
        // this.toastService.openSnackBar("Excel Uploaded Successfully")
        this.toastService.toastMsg({
          title: "Success",
          content: "Excel Uploaded Successfully!!!"
        });
        // this.getAllParties();
        this.getGroupDetails()
      },(err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: "err.error.error_message)",
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else if(err.error.status == "409" && err.error.expose == false){
          this.toastService.toastMsg({
            title: "Error",
            content: "List Of Parties Already Exists"
          });
          // this.toastService.openErrorSnackBar("List Of Parties Already Exists");
        }
        else{
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
      });
    }
  }

  startUpload(){
    document.getElementById('uploadFile').click();
  }

  // for pagination
  handlePage(e) {
    this.currentPageNo = e.pageIndex;
    this.pagesize = e.pageSize;
    if (this.partyGroupData.length > 0) {
      this.partyGrpPageSize = e.pageSize;
      
    const searchInput = document.getElementById('searchInput') as HTMLInputElement;
    if (searchInput.value !== '') {
      this.searchByGroupName(searchInput.value);
    } else {
      this.getGroupDetails();
    }
    }
  }

  viewModal(itemId): void {
    const dialogRef: MatDialogRef<ViewPartyGroupComponent> = this.dialog.open(ViewPartyGroupComponent, {
      width: '700px',
      height : '100vh',
      data: { itemId: itemId ,
        totalrow : this.totalrow ,
        currentPageNo : this.currentPageNo
      },
      panelClass: 'custom-dialog-view-partyGrp',
      disableClose:true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  onChekedsingleCheck(e, grpId){
    if (e.target.checked) {
      this.selectedPartyGrpIds.push(grpId);
      this.checkSingleArray.push(e.target.value);
    } else {
      const index = this.selectedPartyGrpIds.indexOf(grpId);
      if (index !== -1) {
        this.selectedPartyGrpIds.splice(index, 1);
        this.checkSingleArray.splice(index, 1);
      }
    }

    // const checkArray: FormArray = this.form.get('checkArray') as FormArray;
    // if (e.target.checked) {
    //   this.checkSingleArray.push(e.target.value);
    // }
    // else {
    //   this.checkSingleArray = [];
    // }
    // console.log("checkSingleArray",this.checkSingleArray)
  }

  
  getSingleCheckedId(groupIdId){
    this.groupId = groupIdId;

  }
  onChekMultiple($event){
    const isChecked = $event.target.checked;
      if (isChecked) {
        this.selectedPartyGrpIds = this.partyGroupData.map(item => item.id);
      } else {
        this.selectedPartyGrpIds = [];
      }
      this.checkSelect = isChecked; // Update the checkbox state in the header

    let id = $event.target.value;
    this.isChecked = $event.target.checked;
    this.upiCheckedId = this.partyGroupData.map((item) => {
      if (id == -1) {
        if (this.isChecked == true) {
          this.checkSelectsingle = true;
          return item.id;
        }
        else {
          this.checkSelectsingle = false;
          this.upiCheckedId = [];
        }
      }
    })

  }
  getUPIcheckId(groupIdId){
    this.groupId = groupIdId;

  }
  showDeleteButton(): boolean {
    return this.selectedPartyGrpIds.length > 0;
  }

  deletMultipleGroupId(){
    let request = {
      "deleteGroups": this.groupId

    }
    this.partyGroupservice.multipleGroupDelete(request, this.currentUser.id).then((res:any)=>{
      // this.toastService.openSnackBar("Group Deleted Successfully!!!");
      this.toastService.toastMsg({
        title: "Success",
        content: "Group Deleted Successfully!!!"
      });
      this.checkSelectsingle = false;
      this.upiCheckedId = [];
      this.checkSelect=false;
      this.allSelect=false;
      this.isChecked=false;
      this.checkSingleArray= [];
      this.getGroupDetails() 

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }

}
