import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { PartGroupServiceService } from 'src/app/core/services/part-group-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-view-party-group',
  templateUrl: './view-party-group.component.html',
  styleUrls: ['./view-party-group.component.scss']
})
export class ViewPartyGroupComponent implements OnInit {
  groupId: any;
  partyDataDetails: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public currentpage: any;
  public totalrow: number = 10;
  public toatlPages: any;
  public currentPageNo: number = 0;
  public partyGrpPageSize: number = 5;
  public pagesize: number = 1;
  groupDescription: any;
  groupName: any;
  partyGroup: any[];

  constructor(public dialogRef: MatDialogRef<ViewPartyGroupComponent>,
    @Inject(MAT_DIALOG_DATA) public partyData: any, private partyGroupservice: PartGroupServiceService, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,) { }

  ngOnInit(): void {
    this.groupId = this.partyData.itemId
    this.currentPageNo = this.partyData.currentPageNo
    this.totalrow = this.partyData.totalrow
    this.getGroupData()
  }

  getGroupData(){
    this.partyGroupservice.getPartiGroupsDetails({}, this.currentUser.id, this.totalrow, (this.currentPageNo + 1)).then((res) => {
      console.log(res.data);
      this.partyDataDetails = res.data.pageData.filter((data) => data.group.id === this.groupId).map((data) => data.group);
      console.log(this.partyDataDetails)
      this.groupDescription = this.partyDataDetails[0].groupDescription
      this.groupName = this.partyDataDetails[0].groupName
      this.partyGroup = this.partyDataDetails[0].parties.map(data => data.partyName);
    },(err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

  cancel() {
    this.dialogRef.close();
  }
}
